<template>
  <div class="login">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-12 col-sm-12 col-md-12 col-lg-6 bg-app">
          <form>
            <div class="form-auth">
              <h1>Register</h1>
              <div class="form-group">
                <div
                  class="alert alert-danger"
                  role="alert"
                  v-if="message.error"
                >
                  {{ message.error }}
                </div>
              </div>
              <div class="form-group">
                <div
                  class="alert alert-success"
                  role="alert"
                  v-if="message.success"
                >
                  {{ message.success }}
                </div>
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputEmail1"
                  @keyup.enter.prevent="createUser"
                  v-model="newUser.email"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                />
                <small id="emailHelp" class="form-text text-muted"
                  >We'll never share your email with anyone else.</small
                >
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Password</label>
                <input
                  type="password"
                  class="form-control"
                  id="exampleInputPassword1"
                  @keyup.enter.prevent="createUser"
                  v-model="newUser.password"
                  placeholder="Password"
                />
              </div>
              <div class="form-group mb-4">
                <label for="exampleInputName">Name</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputName"
                  v-model="newUser.name"
                  @keyup.enter.prevent="createUser"
                  placeholder="Name"
                />
              </div>
              <div class="row">
                <div class="col-6">
                  <a
                    v-bind:href="'/message-app/#/login'"
                    class="btn btn-outline-light w-100"
                    v-text="'Back'"
                  />
                </div>
                <div class="col-6">
                  <button
                    @click.prevent="createUser"
                    class="btn btn-custom"
                    v-text="'Register'"
                  />
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import fire from "../store/fire";
// Register a global custom directive called `v-focus`
Vue.directive("focus", {
  inserted(el) {
    // Focus the element
    el.focus();
  }
});

export default {
  name: "Home",

  data() {
    return {
      newUser: {
        email: "",
        password: "",
        name: ""
      },
      message: {
        success: "",
        error: ""
      }
    };
  },

  methods: {
    createUser() {
      fire
        .auth()
        .createUserWithEmailAndPassword(
          this.newUser.email,
          this.newUser.password
        )
        .then(data => this.addUser(data))
        .catch(err => {
          this.message.error = err.message;
        });
    },

    addUser(data) {
      console.log(data.user.uid)
      fire
        .database()
        .ref("/users")
        .child(`${data.user.uid}`)
        .update({ name: `${this.newUser.name}`, remember: false })
        .then(() => {
          this.message.success = "Register Successfully";
          setTimeout(function() {
            window.location.href = "/message-app/#/login";
          }, 2000);
        });
    }
  }
};
</script>
<style>
@media screen and (max-width: 992px) {
  .bg-app {
    margin: 0 !important;
    height: 100vh !important;
  }
}
</style>
