<script>
import Vue from "vue";
import fire from "@/store/fire";
// import fire from "../store/fire";
// Register a global custom directive called `v-focus`
Vue.directive("focus", {
  inserted(el) {
    el.focus();
  }
});

export default {
  name: "Room",
  // HelloWorld
  components: {},

  data() {
    return {
      btnRemove: false,
      interval: false,
      count: 0,
      rooms: [],
      newRoom: {
        name: "",
        listUsers: []
      },
      error: ""
    };
  },

  beforeCreate() {
    let checkCurrentUser =
      localStorage.getItem(this.$store.state.STORAGE_KEY) || null;
    if (checkCurrentUser == null) window.location.href = "/message-app/#/login";
  },

  watch: {},

  created() {},

  mounted() {
    this.fetch();
  },

  updated() {},

  methods: {
    removeRoom(id) {
      fire
        .database()
        .ref("/comments")
        .orderByChild("uidRoom")
        .equalTo(id)
        .on("value", snapshot => {
          let data = snapshot.val();
          if (data != null) {
            console.log(data);
            Object.keys(data).forEach(key => {
              fire
                .database()
                .ref("/comments/" + key)
                .remove();
            });
          }
        });
      fire
        .database()
        .ref("/rooms/" + id)
        .remove();

      this.fetch();
    },
    start(index) {
      if (!this.interval) {
        this.interval = setInterval(() => {
          this.rooms.map(val => (val.isDelete = false)),
            (this.rooms[index].isDelete = true);
        }, 500);
      }
    },
    stop() {
      clearInterval(this.interval);
      this.interval = false;
    },
    addRoom() {
      fire
        .database()
        .ref("/rooms")
        .push({
          name: this.newRoom.name,
          listUsers: [this.fetchLocalStorage()]
        });
      this.newRoom.name = "";
    },
    fetchLocalStorage() {
      return JSON.parse(
        localStorage.getItem(this.$store.state.STORAGE_KEY) || null
      );
    },
    filter(list) {
      let list1 = [];
      list.forEach(val1 => {
        if (
          val1.listUser.filter(val2 => val2 === this.fetchLocalStorage())[0] !==
          undefined
        ) {
          list1.push(val1);
        }
      });
      return list1;
    },
    fetch() {
      fire
        .database()
        .ref("/rooms")
        .on("value", snapshot => {
          let listRoom = [];
          let data = snapshot.val();
          let listUser = [];
          if (data != null) {
            Object.keys(data).forEach(key => {
              listUser = [];
              data[key].listUsers.map(val => listUser.push(val));
              listRoom.push({
                id: key,
                name: data[key].name,
                listUser: listUser,
                isDelete: false
              });
              this.rooms = this.filter(listRoom);
            });
          } else {
            this.rooms = [];
          }
        });
    }
  }
};
</script>
