<template>
  <div class="login">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-12 col-sm-12 col-md-12 col-lg-6 bg-app">
          <form>
            <div class="form-auth">
              <h1>Login</h1>
              <div class="form-group">
                <div class="alert alert-danger" role="alert" v-if="error">
                  {{ error }}
                </div>
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputEmail1"
                  v-model="email"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                />
                <small id="emailHelp" class="form-text text-muted"
                  >We'll never share your email with anyone else.</small
                >
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Password</label>
                <input
                  type="password"
                  @keyup.enter.prevent="signInWithEmailAndPassword"
                  class="form-control"
                  id="exampleInputPassword1"
                  v-model="password"
                  placeholder="Password"
                />
              </div>

              <div class="form-group form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="exampleCheck1"
                  @keyup.enter.prevent="signInWithEmailAndPassword"
                />
                <label class="form-check-label" for="exampleCheck1"
                  >Check me out</label
                >
              </div>
              <div class="row">
                <div class="col-6">
                  <a
                    v-bind:href="'/message-app/#/register'"
                    class="btn btn-outline-light w-100"
                    v-text="'Register'"
                  />
                </div>
                <div class="col-6">
                  <button
                    @click.prevent="signInWithEmailAndPassword"
                    class="btn btn-custom"
                    v-text="'Login'"
                  />
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import fire from "../store/fire";
// Register a global custom directive called `v-focus`
Vue.directive("focus", {
  inserted(el) {
    // Focus the element
    el.focus();
  }
});

export default {
  name: "Home",
  // HelloWorld
  components: {},

  data() {
    return {
      listComments: [],
      email: "",
      password: "",
      error: ""
    };
  },

  beforeCreate() {},

  watch: {},

  created() {},

  updated() {},

  methods: {
     signInWithEmailAndPassword() {
      fire
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(data => {
          this.saveLocalStorage(data.user.uid);
        })
        .catch(err => {
          this.error = err.message;
          this.email = "";
          this.password = "";
        });
    },

    saveLocalStorage(currentUser) {
      localStorage.setItem(
        this.$store.state.STORAGE_KEY,
        JSON.stringify(currentUser)
      );
      setTimeout(function (){
        window.location.href = "/message-app/#/room";
      },1000)
    }
  }
};
</script>
